.herobody_container {
    padding: 15px;
    margin: 35px;
    margin-bottom: 18px;
    margin-top: 18px;
    font-size: 15px;
}

.herobody_Card {
    border-radius: 15px;
    z-index: auto;
    background-color: rgba(255, 255, 255, 0.747);
    padding: 10px;
    margin: 35px;
    box-shadow: 0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25), 0 8px 16px -8px hsla(0, 0%, 0%, 0.3), 0 -6px 16px -6px hsla(0, 0%, 0%, 0.03);
    transition: all ease 200ms;
}

.herobody_Card:hover {
    /* transform: scale(1.010); */
    box-shadow: 0 13px 40px -5px rgba(130, 130, 202, 0.12), 0 8px 32px -8px hsla(0, 0%, 0%, 0.11), 0 -6px 32px -6px hsla(0, 0%, 0%, 0.02);
}

.herobody_p {
    /* padding: 15px;
    margin: 35px; */
    font-size: 15px;
    color: rgb(71, 71, 71);
    /* margin-bottom: 0px; */
}

.herobody_h2 {
    font-size: 20px;
    padding: 15px;
    margin: 35px;
    margin-top: 0px;
    margin-bottom: 18px;
}

.herobody_h2_intro {
    margin-top: 18px;
}

.HeroBODy_div {
    background-color: #f5f7fa;
}


.cards {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.card {
    /* background: #fff; */
    width: 24em;
    border-radius: 0.6em;
    margin: 1em;
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25), 0 8px 16px -8px hsla(0, 0%, 0%, 0.3), 0 -6px 16px -6px hsla(0, 0%, 0%, 0.03);
    transition: all ease 200ms;
}

.card:hover {
    /* transform: scale(1.03); */
    box-shadow: 0 13px 40px -5px hsla(240, 30.1%, 28%, 0.12), 0 8px 32px -8px hsla(0, 0%, 0%, 0.14), 0 -6px 32px -6px hsla(0, 0%, 0%, 0.02);
}

.card img {
    width: 100%;
    object-fit: cover;
}

