video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
    /* background: url('../../public/pictures/pexels-artem-beliaikin-853199.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgb(0, 0, 0, 0, 2);
    object-fit: contain;
    justify-content: center;
}

.hero-container>h1 {
    color: white;
    font-size: 55px;
    margin-top: 15px;
    font-weight: 300;
}

.hero-container>p {
    margin-top: 10px;
    font-size: 18px;
    color: rgba(255, 255, 255, 0.692);
    font-family: 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 100;

}

.hero-btns {
    margin-top: 30px;
}

.hero-btns .btn {
    margin: 6px;
}

.fa-play-circle {
    margin: auto;
    /* height: 25px;
    width: 25px; */
}

/* .herosection_p{
    font-size: 8px;
} */
@media screen and (max-width: 1024px) {
    .hero-container>h1 {
        color: white;
        font-size: 45px;
        margin-top: 15px;
        font-weight: 200;
    }

    .hero-container>p {
        margin-top: 10px;
        font-size: 15px;
        color: rgba(255, 255, 255, 0.692);
        font-family: 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: 100;

    }

}

@media screen and (max-width: 960px) {
    .hero-container>h1 {
        color: white;
        font-size: 40px;
        margin-top: 15px;
        font-weight: 200;
    }

    .hero-container>p {
        margin-top: 10px;
        font-size: 13px;
        color: rgba(255, 255, 255, 0.692);
        font-family: 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: 100;

    }

    .hero-btns {
        margin-top: 20px;
    }

}

@media screen and (max-width: 768px) {
    .hero-container>h1 {
        color: white;
        font-size: 40px;
        margin-top: 15px;
        font-weight: 200;
    }

    .hero-container>p {
        margin-top: 10px;
        font-size: 13px;
        color: rgba(255, 255, 255, 0.692);
        font-family: 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: 100;

    }

    .hero-btns {
        margin-top: 20px;
    }

    .btn {
        font-size: 15px;
        width: 100%;
    }

    .btn-mobile {
        display: block;
        text-decoration: none;

    }

}

@media screen and (max-width: 500px) {
    .hero-container {
        height: 100vh;
    }
    .hero-container>h1 {
        color: white;
        font-size: 25px;
        margin-top: 15px;
        font-weight: 200;
    }

    .hero-container>p {
        margin-top: 10px;
        font-size: 8px;
        color: rgba(255, 255, 255, 0.692);
        font-family: 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: 100;

    }

    .hero-btns {
        margin-top: 20px;
    }

    .btn {
        font-size: 10px;
    }
}