.Team_heading {
  padding: 2%;
  padding-top: 8%;
  font-size: 20px;
}

.Team_sub_heading {
  padding: 1.5%;
  padding-top: 5%;
  font-size: 15px;
}

.sub_hr {
  width: 100%;
    border: 0;
    height: 1px;
    background: #ffffffcf;
    background-image: linear-gradient(to right, #ccc, #333, #ccc);
}

.Team_sub_sub_heading {
  display: flex;
  justify-content: center;
  padding: 1.2%;
  padding-top: 2%;
  font-size: 10px;
}

.sub_sub_hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0)38%, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0)62%);
}

.Heading_class {
  /* width: 80%; */
  /* display: flex; */
}

/* hr {
    width: 80%;
    display: flex;

} */

.Team_container {
  margin: 3.625rem 11.2vw;
  height: 1500px;
}

.Student_body {
  padding: 13px;
  height: -moz-fit-content;
  height: fit-content;
  margin: -1px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-around;
  align-items: center;
}





