.Member_card {
    height: 344px;
    width: 186px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    justify-content: space-evenly;
    align-items: stretch;
    border-radius: 12px;
    object-fit: cover;
    /* display: flex; */
    flex-flow: column;
    /* width: 80%; */
    box-shadow: 0 6px 20px rgb(56 125 255 / 17%);
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 15px;
    overflow: hidden;
    text-decoration: none;
    justify-content: flex-start;
}

.Member_img {
    width: 187px;
    height: 70%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    align-items: center;
    justify-content: space-evenly;
    z-index: -1;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    object-fit: cover;
    transition: all 0.2s linear;
}

.Member_img:hover {
    display: flex;
    transform: scale(1.05);
    cursor: pointer;
}

.Member_info_container {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: stretch;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 10px;
    height: 196px;
}

.Linkedin_icon {
    padding: 2px;
    height: 29px;
    width: 36px;
    font-size: 20px;
    cursor: pointer;
}

.Member_name {
    font-size: 14px;
}

.Member_inmfo {
    font-size: 12px;
    color: rgb(103, 104, 104);
}






